body {
  margin: 0;
  padding: 0;
  font-family:'Roboto Slab',serif;
  text-rendering:optimizeLegibility;
  -webkit-font-smoothing:antialiased;
}

.wrapper {
  position: absolute;
  min-width: 100%;
  min-height: 100%;
  /*background-color: #2b4162;*/
  /*background-image: linear-gradient(315deg, #2b4162 0%, #12100e 74%);*/

  /*background-color: #ffffff;*/
  /*background-image: linear-gradient(315deg, #ffffff 0%, #274060 74%);*/

  background-color: #4d4855;
  background-image: -webkit-linear-gradient(303deg, #4d4855 0%, #000000 74%);
  background-image: linear-gradient(147deg, #4d4855 0%, #000000 74%);

  background-repeat: no-repeat;

  background-size: cover;
  background-position: 200% 100%;
}

h1 {
  font-weight: 900;
  font-size: 30px;
  line-height: 1.4;
  margin-top: 14px;
  max-width: 600px;
  width: 80%
}

h2 {
  font-size: 17px;
  font-weight: 400;
  line-height: 1.4;
  margin-top: 8px;
  max-width: 600px;
  width: 80%
}

.Timeline {
    line-height: 1.4;
    font-size: 17px;
    font-weight: 400;
}

.Projects {
    margin-top: 150px;
    line-height: 1.4;
    font-size: 17px;
    font-weight: 400;
}

.Project, .Other {
    margin-top: 8px;
    line-height: 28px
}


.Link {
    text-decoration: none;
    -webkit-text-decoration-color: inherit;
            text-decoration-color: inherit;
    border-color: inherit;
    border-style: dotted;
    border-width: 0 0 1px;
    transition: border-color .2s ease-in-out;
    -moz-transition: border-color .2s ease-in-out;
    -webkit-transition: border-color .2s ease-in-out;
    color: floralwhite;
}

.Resume {
    text-decoration: none;
    -webkit-text-decoration-color: inherit;
            text-decoration-color: inherit;
    border-color: inherit;
    border-style: dotted;
    border-width: 0 0 1px;
    transition: border-color .2s ease-in-out;
    -moz-transition: border-color .2s ease-in-out;
    -webkit-transition: border-color .2s ease-in-out;
    color: skyblue;
}

.Title {
    font-size: 20px;
    font-weight: 400;
    color: aqua;
    margin-top: 50px;
}

.Year {
    margin-top: 8px;
    line-height: 28px
}

.Work {
    width: 70%;
    float:right
}

.Timestamp {
    width: 30%;
    float:left
}

.slide {
    width: 600px;
    height: 465px;
}

.image_frame {
    margin-top: 70px;
    margin-right: 150px;
}



.App {
  left: 8%;
  top: 10%;
  right: 1%;
  position:absolute;
  color: floralwhite;
}

.Left {
  float: left;
  width: 42%;
}

.Right {
  float: left;
  width: 58%;
}

.image_wide {
  margin-top: 70px;
  height: 500px;
  width: 700px;
}

.image_high {
  margin-left: 50px;
  height: 700px;
  width: 500px;
}

.map {
  margin-top: 30px;
}

.map_title {
  text-align: left;
  font-size: 20px;
  font-weight: 800;
}
